body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-23xl: 42px;
  --font-size-lg: 18px;
  --font-size-13xl: 32px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-mediumblue: #0249ff;
  --color-gainsboro: #d9d9d9;
  --color-black: #000;

  /* Paddings */
  --padding-10xs: 3px;
  --padding-9xl: 28px;
  --padding-xl: 20px;

  /* Border radiuses */
  --br-3xs: 10px;
}
